import React, { useRef } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

interface CarouselProps {
    images: { src: string; alt?: string }[];
    speed?: number;
    textOverlay?: {content: string; positionClass?: string}[];
    classText?: string;
  }
  
  const Carousel: React.FC<CarouselProps> = ({ images, speed = 2000, textOverlay = [], classText }) => {
    const sliderRef = useRef<Slider | null>(null);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: speed,
    pauseOnHover: false,
  };

  const handleMouseEnter = () => {
    if(sliderRef.current) {
        sliderRef.current.slickPause();
    }
};
const handleMouseLeave = () => {
      if(sliderRef.current) {
          sliderRef.current.slickPlay();
      }
  };

  return (
    <div className={styles.carouselContainer} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      <Slider {...settings} ref={sliderRef}>
        {images.map((image, index) => (
          <div key={index} className="relative -mb-2">
            <img src={image.src} alt={image.alt || `Slide ${index + 1}`} className={styles.carouselImage} />
            {textOverlay[index]?.content && (
              <div
                className={`${styles.carouselText} ${textOverlay[index].positionClass || ""} ${classText}`}
              >
                {textOverlay[index].content}
              </div>
            )}
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default Carousel;

const styles = {
    carouselContainer: "w-full max-w-[1024px] mx-auto relative rounded-lg shadow overflow-hidden sm:overflow-visible",
    carouselImage: "w-full rounded-lg",
    carouselText: "absolute text-white text-2xl sm:text-6xl font-bold poppins-bolder text_shadow"
}